@import url(https://fonts.googleapis.com/css2?family=Corinthia&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Corinthia&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Corinthia&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  margin: 10px;
  width: 18rem;
  border-radius: 10px;
}

.card #imgCard {
  height: 12rem;
  width: 90%;
  margin: 10px;
  border: 1px solid lightgrey;
  border-radius: 20px;
}

.card .itemNavLink {
  display: flex;
  justify-content: center;
  color: white;
  text-decoration: none;
}

.floatButton {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  z-index: 5;
  position: fixed;
  bottom: 10%;
  right: 5%;
  text-decoration: none;
}

.floatButton .taza {
  position: absolute;
  margin-left: 15px;
  bottom: 35px;
  width: 5rem;
  height: 5rem;
}

.floatButton .btn {
  margin-top: 0;
  font-size: 1rem;
  width: 100%;
  height: 2rem;
}
/*# sourceMappingURL=App.css.map */
.NavBar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: burlywood;
  border: 1px solid black;
  height: 4rem;
}

.NavBar #titleApp {
  padding-top: 10px;
  font-family: 'Corinthia', cursive;
  font-size: 2.5rem;
}

.NavBar #titleApp:hover {
  transform: scale(1.5);
}

.NavBar .link {
  padding: 0px 10px;
  height: 3rem;
  background-color: cornsilk;
  font-family: 'Corinthia', cursive;
  border-radius: 50%;
}

.NavBar .link:hover {
  background-color: greenyellow;
}

.NavBar a {
  font-size: 2rem;
  font-weight: 7;
  text-decoration: none;
  color: black;
  margin-bottom: 20px;
}

.NavBar #logoCafe {
  width: 4rem;
  height: 2.5rem;
  border-radius: 30%;
}

.NavBar #logoCafe:hover {
  transform: scale(1.5);
}

.NavBar .cartWidget {
  display: flex;
  flex-direction: row;
  height: 2.5rem;
  border: 1px solid black;
  border-radius: 20px;
  background-color: cornsilk;
}

.NavBar .cartWidget .contador {
  padding: 10px 0 0 20px;
  font-weight: 500;
}

.NavBar .btn-reset {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  font-size: 1rem;
  border-radius: 20px;
}
/*# sourceMappingURL=NavBarStyles.css.map */
.detailContainer {
  display: flex;
  color: rosybrown;
  margin: 10px;
  width: 99%;
  height: 100%;
  background: #8E0E00;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #1F1C18, #8E0E00);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 10px;
}

.detailContainer #banderaDetail {
  margin: 20px 5px;
  border-radius: 10%;
}

.detailContainer .col {
  margin: 10px;
}

.detailContainer .purchaseControl {
  display: flex;
  flex-direction: column;
}

.detailContainer #purchaseEndBadge {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
}
/*# sourceMappingURL=ItemDetailContainer.css.map */
.ItemCount {
  display: flex;
  flex-direction: row;
  margin-top: 1rem;
  max-width: 250px;
  padding: 10px 0px 10px 15px;
  border-radius: 10px;
  background-color: burlywood;
}

.ItemCount .counter {
  width: 50%;
}

.ItemCount .counter input {
  text-align: center;
  font-weight: 500;
}

.ItemCount input {
  width: 95%;
  padding: 0;
}

.ItemCount .button-mas {
  max-width: 2.5rem;
  margin-left: 12px;
  height: 30px;
}

.ItemCount .button-menos {
  max-width: 2.5rem;
  margin-left: 14px;
  height: 30px;
}

.ItemCount .btn-verde {
  font-size: .7rem;
  width: 4.5rem;
  margin-left: 10px;
}

.ItemCount .btn-verde .carritoMas {
  width: 80%;
}
/*# sourceMappingURL=ItemCountStyle.css.map */
.listContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.listContainer .addedItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: 'Corinthia';
  font-size: 3rem;
}

.listContainer .addedItem img {
  margin: 10px;
  border-radius: 50px;
  width: 20%;
  height: 5rem;
}

.listContainer .addedItem .name {
  width: 30%;
}

.listContainer .addedItem .qty {
  padding: 0%;
  width: 10%;
}

.listContainer .addedItem button {
  border-radius: 20px;
  font-size: 2rem;
  margin: 0%;
}

.listContainer .addedItem .acum {
  margin-left: 4%;
  width: 10%;
}

.failMessage {
  margin: 20px;
  font-family: 'Corinthia';
  font-size: 3rem;
}

.failMessage .failNav {
  text-decoration: none;
  color: black;
}

.modalContainer {
  position: absolute;
  z-index: 30;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(248, 246, 246, 0.7);
}

.modalContainer .purchaseModal {
  position: absolute;
  padding: 20px;
  z-index: 40;
  width: 40%;
  top: 10%;
  left: 30%;
  border-radius: 30px;
  background-color: rgba(226, 235, 148, 0.9);
}

.modalContainer .purchaseModal .modalButtons {
  display: flex;
  justify-content: flex-end;
}

.modalContainer .purchaseModal .modalButtons .cancelar {
  margin-right: 20px;
}
/*# sourceMappingURL=Cart.css.map */
